































import 'reflect-metadata'
import { Vue, Component, Prop } from 'vue-property-decorator'
import { IAddress } from '@/services/addresses/models'
import { toDJTX } from '@/helper'
import BalanceRow from '@/components/Address/BalanceRow.vue'
import { IBalanceX } from '@/services/addresses/models'
import Big from 'big.js'
import BN from 'bn.js'

@Component({
    components: {
        BalanceRow,
    },
    filters: {
        toDJTX,
        BN,
    },
})
export default class AddressDats extends Vue {
    @Prop() assets!: IBalanceX[]
    @Prop() addresses!: IAddress[]
    @Prop() djtxBalance!: BN
    @Prop() links?: boolean
    @Prop() title?: string

    get headers(): any[] {
        return [
            { text: 'Address', value: 'address' },
            {
                text: 'DJTX Balance',
                value: 'balance',
            },
        ]
    }
}
